import React, { useState, useEffect } from "react";
import fetch from "isomorphic-unfetch";

const Results = ({ newEntry, scoreData, choSpecificHighscores }) => {
  const results = [...scoreData].sort((x, y) => x.score - y.score);

  const choSpecificHighscoresResults = [...choSpecificHighscores].sort(
    (x, y) => x.score - y.score
  );

  return (
    <div>
      <h3>Absolute top 5 (van alle foto's)</h3>
      <table>
        <thead>
          <tr>
            <td>Username:</td>
            <td>Score:</td>
          </tr>
        </thead>
        <tbody>
          {results.slice(0, 5).map(
            ({ username, score }) =>
              score && (
                <tr
                  style={{
                    color: username === "Jouw naam hier" ? "red" : "black"
                  }}
                  key={Math.random()}
                >
                  <td>{username}</td>
                  <td>{score}</td>
                </tr>
              )
          )}
        </tbody>
      </table>
      <h3>Highscores van deze foto:</h3>
      <table>
        <thead>
          <tr>
            <td>Username:</td>
            <td>Score:</td>
          </tr>
        </thead>
        <tbody>
          {choSpecificHighscoresResults.map(
            ({ username, score }) =>
              score && (
                <tr
                  style={{
                    color: username === "Jouw naam hier" ? "red" : "black"
                  }}
                  key={Math.random()}
                >
                  <td>{username}</td>
                  <td>{score}</td>
                </tr>
              )
          )}
        </tbody>
      </table>
    </div>
  );
};

export default Results;
